// Objects
class Ball {
  constructor(x, y, radius, color) {
    this.x = x
    this.y = y
    this.radius = radius
    this.color = color
    this.dy = 0
  }

  draw() {
    c.beginPath()
    c.arc(this.x, this.y, this.radius, 0, Math.PI * 2, false)
    c.fillStyle = this.color
    c.fill()
    c.closePath()
  }
  moveBall(){
   
    if(this.y + this.radius + this.dy > canvas.height){
      this.dy = -this.dy * friction
    }else if(this.y + this.radius<canvas.height){
      this.dy += gravity
    }
    this.y+=this.dy;
  }

  update() {
    this.moveBall()
    this.draw()
  }
}

class Circle {
  constructor(size){
    this.size = size
    this.startposition = 0
    this.endposition = Math.PI/2
  }
  clickToPosition(x,y){
    console.log(`${x} and ${y}`)
  }
  
  draw(){
    c.beginPath()
    c.arc(canvas.width/2,canvas.height/2,this.size,this.startposition,this.endposition);
    c.stroke()
  }

  update(){
    this.draw();
  }

}

